* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  font-family: "Handlee", cursive;

  font-size: x-large;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-top: 1em;
}
p {
  font-size: medium;
}
a {
  text-decoration: none;
}
:visited {
  color: rgb(33, 33, 33);
}
:hover {
  color: rgb(33, 33, 33);
}
